import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-support',
  template: `<div class="support-wrapper">
  <div class="title-container">
      <h1 class="title">Contact Us!</h1>
  </div>

  <form class="form-container" (ngSubmit)="processForm()">
      <label class="label">Name</label><br>
      <input type="text" name="name" class="input" placeholder="What's Your Name?" required [(ngModel)]="name"><br>

      <label class="label">Email</label><br>
      <input type="email" name="email" class="input" placeholder="What's Your Email?" required [(ngModel)]="email"><br>

      <label class="label">Message</label><br>
      <textarea name="message" class="textarea" placeholder="What Would You Like to Share?" required [(ngModel)]="message"></textarea><br>

      <button type="submit" class="btnSubmit">
          Send
      </button>
  </form>
</div>`,
  styleUrls: ['./support.component.css']
})
export class SupportComponent implements OnInit {

  name: string;
  email: string;
  message: string;

  constructor() {
    this.name = "";
    this.email = "";
    this.message = "";
  }

  ngOnInit() {
    
  }

  processForm() {
    const allInfo = `My name is ${this.name}, email is ${this.email} and my message is ${this.message}`;
    alert(allInfo);
  }

}
